.page {
  min-width: 320px;
  background: $c-gray;
  margin: 0;

  &--with-menu {
    overflow: hidden;
  }

  &__picture {
    width: 100%;
    display: flex;
    justify-content: center;
    height: 356px;
    overflow: hidden;
    margin-bottom: 20px;

    @include hover (scale, 105%);

    @include onTablet {
      height: 420px;
      margin-bottom: 30px;
    }

    @include onDextop {
      margin-bottom: 20px;
    }
  }

  &__img {
    height: 100%;
    object-fit: cover;
    object-position: center;
    width: 100%;
  }

  &__subtitle {
    margin-bottom: 20px;
  }
}
