.subscribe {
  margin-bottom: 80px;

  @include onTablet {
    margin-bottom: 128px;
  }

  @include onDextop {
    margin-bottom: 200px;
  }

  &__title {
    margin-bottom: 20px;
    color: $black;

    @include onDextop {
      margin-bottom: 50px;
    }
  }

  &__text {
    margin-bottom: 50px;
  }

  &__email {
    width: 100%;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid $black;
    background: none;
    font-family: Raleway "IBM Plex Sans", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    outline: none;

    @include onDextop {
      margin-bottom: 40px;
    }

    &::placeholder {
      font-weight: 400;
    }

    &:hover {
      color: $red;
      border-color: $red;

      &::placeholder {
        color: $red;
      }
    }

    &:focus {
      color: $red;
      border-color: $red;
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {
      -webkit-box-shadow: 10px 10px 10px 20px $c-gray inset;
    }
  }

  &__button {
    @include onTablet {
      width: calc((100% - 90px) * 0.75 + 60px);
    }

    @include onDextop {
      width: calc((100% - 120px) / 5 * 3 + 60px);
    }
  }
}
