.visit-museum {
  background-color: $b-gray;
  padding: 50px 0;
  margin-bottom: 90px;

  @include onTablet {
    padding: 205px 0;
    margin-bottom: 125px;
  }

  @include onDextop {
    margin-bottom: 110px;
    background-image: url(../images/img_07.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__text {
    margin-bottom: 40px;
    color: $white;

    @include onTablet {
      margin-bottom: 60px;
    }

    @include onDextop {
      margin-bottom: 50px;
    }
  }

  &__button {
    margin: 0;
  }
}
