.menu {
  background-color: $b-gray;
  transform: translateY(-100%);
  transition: transform 0.3s;
  width: 100%;
  position: fixed;
  top: 0;

  @include onDextop {
    height: 100vh;
  }

  &:target {
    transform: none;
    z-index: 2;
  }

  &__margin {
    margin-bottom: 10px;

    @include onTablet {
      margin-bottom: 27px;
    }

    @include onDextop {
      margin-bottom: 13px;
    }
  }

  &__language {
    display: flex;

    @include onTablet {
      order: 2;
    }
  }

  &--close {
    display: block;
    height: 25px;
    width: 25px;
    background-image: url(../images/cross.svg);
    background-position: center;
    background-repeat: no-repeat;

    @include hover (scale, 110%);

  }

  &__text {
    font-size: 16px;
    font-weight: 600;
    line-height: 150%;
    margin-bottom: 20px;
    color: $white;

    &--adress {
      display: block;
      margin-bottom: 40px;
      text-decoration: none;
      color: $white;

      @include hover (color, $red);
    }

    @include onTablet {
      margin-bottom: 52px;
    }

    @include onDextop {
      margin-bottom: 71px;
    }
  }

  &__link {
    text-decoration: none;
    color: $white;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 20px;

    @include hover (color, $red);

    @include onTablet {
      font-size: 48px;
      line-height: 120%;
      margin-bottom: 30px;
    }

    @include onDextop {
      margin-bottom: 60px;
    }

    &--last {
      margin-bottom: 40px;

      @include onTablet {
        margin-bottom: 50px;
      }

      @include onDextop {
        margin-bottom: 60px;
      }
    }
  }

  &__line {
    display: block;
    height: 1px;
    width: 100%;
    background-color: $white;
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 50px;
    }

    @include onDextop {
      margin-bottom: 60px;
    }
  }

  &__button {
    margin-bottom: 238px;

    @include onTablet {
      margin-bottom: 30px;
    }

    @include onDextop {
      margin-bottom: 108px;
    }
  }

  &__picture {
    display: none;

    @include onTablet {
      display: block;
      height: 466px;
      width: 422px;
      margin: 0 auto;
      background-image: url(../images/img_02.svg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }

    @include onDextop {
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc((100% - 30px) / 2);
      height: 697px;
      background-size: contain;
    }
  }
}
