.footer {
  background-color: $b-gray;
  padding: 34px 0 30px;

  @include onTablet {
    padding-top: 50px;
  }

  &__margin {
    margin-bottom: 50px;
  }

  &__subtitle {
    font-family: Playfair Display "IBM Plex Sans", sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;
    margin-bottom: 10px;
    color: $white;

    @include onTablet {
      margin-bottom: 30px;
    }
  }

  &__text {
    display: block;
    text-decoration: none;
    color: $white;

    @include hover (color, $red);

    &--margin {
      margin-bottom: 32px;

      @include onTablet {
        margin-bottom: 50px;
      }
    }
  }

  &__socials {
    display: flex;
    gap: 20px;
  }

  &__link {
    height: 20px;
    width: 20px;
    fill: $white;

    @include hover(fill, $red);
  }

  &__schedule {
    color: $white;
  }

  &__gap {
    margin-bottom: 5px;

    @include onTablet {
      margin-bottom: 10px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__left {
    display: flex;
    gap: 22px;

    @include onDextop {
      gap: 102px;
    }
  }

  &__scroll-up {
    @include hover (fill, $red);
    @include hover (scale, 115%);
    // height: 500px;
  }
}
