.upcoming-events {
  margin-bottom: 90px;

  @include onTablet {
    margin-bottom: 100px;
  }

  @include onDextop {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 60px;
    color: $black;

    @include onTablet {
      margin-bottom: 76px;
    }

    @include onDextop {
      margin-bottom: 70px;

    }
  }

  &__picture {

    @include onDextop {
      margin-bottom: 60px;
      height: 475px;
    }
  }

  &__date {
    color: $red;
    margin-bottom: 10px;
  }

  &__text {
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 50px;
    }
  }

  &__button {
    @include onDextop {
      width: calc((100% - 30px) / 2);
    }
  }
}
