.actual-exhibitions {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 164px;
  }

  @include onDextop {
    margin-bottom: 200px;
  }

  &__title {
    margin-bottom: 60px;
    color: $black;

    @include onTablet {
      margin-bottom: 75px;
    }

    @include onDextop {
      margin-bottom: 71px;
    }
  }

  &__picture {

    @include onDextop {
      height: 484px;
    }
  }

  &__date {
    color: $red;
    margin-bottom: 10px;
  }

  &__text {
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 40px;
    }

    @include onDextop {
      margin-bottom: 50px;
    }
  }
}
