.button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  background-color: $red;
  margin-bottom: 60px;

  transition: ease 0.3s;

  @include onTablet {
    margin: 0;
  }

  &:hover {
    background-color: $bt-hover;
  }

  &:active {
    background-color: $bt-pressed;
  }

  &:disabled {
    background-color: $bt-disabled;
  }

  &-margin {
    margin-bottom: 30px;

    @include onDextop {
      margin: 0;
    }
  }

  &--2 {
    gap: 11.33px;
    background: none;
    border: 1px solid $red;
    margin: 0;

    @include onTablet {
      position: absolute;
      width: calc((100% - 150px) / 3 + 30px);
      right: 0;
      top: 0;
    }

    @include onDextop {
      width: calc((100% - 330px) / 4 + 60px);
    }

    &:hover {
      border-color: $bt-hover;
    }

    &:active {
      border-color: $bt-pressed;
    }

    &:disabled {
      border-color: $bt-disabled;
    }
  }
}
