.news {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 160px;
  }

  @include onDextop {
    margin-bottom: 200px;
  }

  &__title {
    margin-bottom: 60px;
    color: $black;

    @include onTablet {
      margin-bottom: 75px;
    }

    @include onDextop {
      margin-bottom: 55px;
    }
  }

  &__picture {
    height: 280px;

    @include onDextop {
      height: 370px;
    }
  }

  &__date {
    margin-bottom: 0;
    color: $black;
  }

  &__text {
    margin-bottom: 50px;

    @include onTablet {
      margin-bottom: 0;
    }

    &--margin {
      margin-bottom: 30px;
    }
  }
}
