h1 {
  font-family: Playfair Display "IBM Plex Sans", sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 100%;
  color: $white;
  margin: 0 0 30px;

  @include onTablet {
    font-size: 72px;
    margin-bottom: 20px;
  }

  @include onDextop {
    font-size: 96px;
    margin-bottom: 30px;
  }
}

h2 {
  font-family: Playfair Display "IBM Plex Sans", sans-serif;
  font-size: 38px;
  font-weight: 700;
  line-height: 100%;
  color: $white;
  margin: 0 0 20px;

  @include onTablet {
    font-size: 62px;
  }

  @include onDextop {
    font-size: 80px;
    margin-bottom: 10px;
  }
}

h3 {
  font-family: Playfair "IBM Plex Sans", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 125%;
  color: $white;
  margin: 0;

  @include onTablet {
    font-size: 40px;
  }

  @include onDextop {
    font-size: 48px;
  }
}

h4 {
  font-family: Playfair Display "IBM Plex Sans", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 125%;
  color: $black;
  margin: 0;
}

h5 {
  font-family: Playfair Display  "IBM Plex Sans", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 150%;
  color: $white;
  margin: 0;
}

h6 {
  font-family: Playfair Display "IBM Plex Sans", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 125%;
  color: $black;
  margin: 0;
}

.button {
  font-family: Raleway  "IBM Plex Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  color: $white;
  text-align: center;
  text-decoration: none;

  &--2 {
    color: $red;

    &:hover {
      color: $bt-hover;
    }

    &:active {
      color: $bt-pressed;
    }

    &:disabled {
      color: $bt-disabled;
    }
  }
}

div {
  font-family: Raleway "IBM Plex Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
}

.additional {
  font-family: Raleway "IBM Plex Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
  color: $white;
  margin: 0;
}
