.header {
  background-color: $b-gray;
  position: relative;
  margin-bottom: 90px;

  @include onTablet {
    margin-bottom: 105px;
  }

  @include onDextop {
    height: 100vh;
    margin-bottom: 110px;
  }

  &__top {
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;

    @include onTablet {
      margin-bottom: 97px;
    }

    @include onDextop {
      height: 90px;
      margin-bottom: 90px;
    }
    
    &--right {
      display: flex;
      gap: 40px;
    }
  }

  &__logo {
    display: block;
    height: 26px;
    width: 117px;
    background-image: url(../images/NAMU.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__menu {
    display: block;
    height: 20px;
    width: 25px;
    background-image: url(../images/menu.svg);
    background-position: center;
    background-repeat: no-repeat;

    @include hover (scale, 110%);
  }

  &__language {
    color: $white;
    display: none;
    width: 44px;
    border: none;
    background: none;
    text-align: left;
    font-size: 16px;
    outline: none;

    cursor: pointer;

    @include hover (color, $red);

    @include onTablet {
      display: block;
    }
  }

  &__date {
    height: 20px;
    margin-bottom: 10px;
  }

  &__text {
    margin-bottom: 40px;
    color: $white;

    @include onTablet {
      margin-bottom: 50px;
    }

    @include onDextop {
      margin-bottom: 81px;
    }
  }

  &__button {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 70px;
    }

    @include onDextop {
      margin-bottom: 135px;
    }
  }

  &__date2 {
    display: none;

    @include onDextop {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 50px;
    }

    &-text {
      color: $white;
    }
  }

  &__line {
    display: block;
    height: 1px;
    width: calc(100% - 101px);
    background-color: $white;

  }

  &__picture {
    display: block;
    width: 87.5%;
    padding-bottom: 97.5%;
    margin: 0 auto;
    background-image: url(../images/img_01.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include onTablet {
      width: 98.5%;
      padding-bottom: 494px;
    }

    @include onDextop {
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc((100% - 30px) / 2);
      padding-bottom: 663px;
      background-size: contain;
    }
  }
}
